<template>
  <section class="bitcat-history-section">
    <div class="container">
      <h1 class="gradient-title">BITCAT History</h1>
      <div class="history-content">
        <div class="image-side">
          <div class="image-container" @click="handleImageClick">
            <div class="image-wrapper">
              <img src="https://solanabitcat.com/data/7sec.png" alt="Bitcat History" class="history-image">
            </div>
            <div class="image-overlay">
              <i class="fas fa-search-plus"></i>
            </div>
          </div>
          <div class="decorative-circle"></div>
        </div>
        <div class="text-content">
          <div class="history-text">
            <h2>
              <i class="fab fa-bitcoin btc-icon"></i>
              Bitcat was created 7 seconds after Bitcoin posted the AI-generated cat mascot on X at 13:43:21 UTC on Nov 15, 2024.
            </h2>
            <p>
              <i class="fab fa-bitcoin btc-icon"></i>
              Bitcat is Bitcoin's mascot and a symbol of luck, growing in a community-driven way.
            </p>
          </div>

          <div class="decorative-circle circle-text"></div>
        </div>
      </div>
    </div>

    <!-- Modal -->
    <Teleport to="body">
      <Transition name="modal">
        <div class="modal" v-if="showModal" @click="handleModalClose">
          <div class="modal-content" @click.stop>
            <button class="close-button" @click="handleModalClose">
              <i class="fas fa-times"></i>
            </button>
            <img src="https://solanabitcat.com/data/7sec.png" alt="Bitcat History" class="modal-image">
          </div>
        </div>
      </Transition>
    </Teleport>
  </section>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'

const showModal = ref(false)

// Отладка
const handleImageClick = () => {
  console.log('Image clicked, showing modal')
  showModal.value = true
  // Блокируем скролл страницы
  document.body.style.overflow = 'hidden'
}

const handleModalClose = () => {
  console.log('Modal closing')
  showModal.value = false
  // Восстанавливаем скролл страницы
  document.body.style.overflow = 'auto'
}

// Обработка клавиши Escape
const handleKeydown = (event) => {
  if (event.key === 'Escape' && showModal.value) {
    handleModalClose()
  }
}

onMounted(() => {
  document.addEventListener('keydown', handleKeydown)
})

onUnmounted(() => {
  document.removeEventListener('keydown', handleKeydown)
  // Восстанавливаем скролл при размонтировании компонента
  document.body.style.overflow = 'auto'
})
</script>

